<template>
	<div v-if="this.recomendation">
		<b-button variant="danger" @click="getRecomendation()">I don't like this s*** recomendation, let me try again!</b-button>
		<b-table :items="this.taulaProperties.recomendation" :fields="this.taulaProperties.fieldsRecomendation">
			<template #cell(index)="data">
				{{ data.index + 1 }}
			</template>
		</b-table>
	</div>
	<div v-else>
		<b-button variant="danger" @click="getRecomendation()">Get a recomendation!</b-button>
		<b-table :items="this.taulaProperties.questionClothes">
			<template #cell(like)="data">
				<b-button variant="success" @click="addToPicked(data.item)" v-html="data.value"></b-button>
			</template>
		</b-table>
	</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data(){
	  return{
		  recomendation: false,
			taulaProperties: {
				clothes: [],
				questionClothes: [],
				answerClothes: [],
				pickedClothes: [],
				preferred: new Map(),
				punctuated: new Map(),
				recomendation: [],
				fieldsRecomendation: [{key: 'index', label: 'TOP'}, 'color', 'buttons', 'size', 'hood', 'neck', 'sleeve'],
			},
			clothesAtributes: {
				colors: ["red", "blue", "green", "yellow", "purple", "black"],
				buttons: ["buttons", "no-buttons"],
				size:["XL", "L", "M", "S", "XS"],
				hood: ["hood", "no-hood"],
				neck: ["peak", "circular", "shirt"],
				sleeve: ["sleeve", "no-sleeve"],
			}
	  }
  },
  methods: {
		createClothes: function(){
			var colorSize = this.clothesAtributes.colors.length;
			var buttonsSize = this.clothesAtributes.buttons.length;
			var sizeSize = this.clothesAtributes.size.length;
			var hoodSize = this.clothesAtributes.hood.length;
			var neckSize = this.clothesAtributes.neck.length;
			var sleeveSize = this.clothesAtributes.sleeve.length;
			for (var i = 0; i < 50; ++i){
				var cloth = {};
				cloth.color = this.clothesAtributes.colors[Math.floor(Math.random() * colorSize)];
				cloth.buttons = this.clothesAtributes.buttons[Math.floor(Math.random() * buttonsSize)];
				cloth.size = this.clothesAtributes.size[Math.floor(Math.random() * sizeSize)];
				cloth.hood = this.clothesAtributes.hood[Math.floor(Math.random() * hoodSize)];
				cloth.neck = this.clothesAtributes.neck[Math.floor(Math.random() * neckSize)];
				cloth.sleeve = this.clothesAtributes.sleeve[Math.floor(Math.random() * sleeveSize)];
				cloth.like = "Like It";
				this.taulaProperties.clothes.push(cloth);
			}
			this.taulaProperties.questionClothes = this.taulaProperties.clothes.slice(0,10);
			this.taulaProperties.answerClothes = this.taulaProperties.clothes.slice(11,50);

		},
		addToPicked: function(item){
			this.taulaProperties.pickedClothes.push(item);
			for (var [, key] of Object.entries(item)) {
				if(this.taulaProperties.preferred.has(key)){
					let value = this.taulaProperties.preferred.get(key);
					this.taulaProperties.preferred.set(key, value+1)
				}
				else{
					this.taulaProperties.preferred.set(key, 1);
				}
			}
		},
		punctuate: function(){
			for (var i = 0; i < this.taulaProperties.answerClothes.length; ++i) {
				var punctuation = 0;
				for (var [, key] of Object.entries(this.taulaProperties.answerClothes[i])){
					if(this.taulaProperties.preferred.has(key)){
						punctuation = punctuation + this.taulaProperties.preferred.get(key);
					}
				}
				this.taulaProperties.punctuated.set(this.taulaProperties.answerClothes[i], punctuation);
			}
		},
		sortByPunctuation: function(){
			var aux = [];
			this.taulaProperties.punctuated[Symbol.iterator] = function* () {
				yield* [...this.entries()].sort((a, b) => a[1] - b[1]);
			}
			for (let [key,] of this.taulaProperties.punctuated) {
				aux.push(key);
			}
			this.taulaProperties.recomendation = aux.slice(aux.length-5, aux.length);
		},
		resetRecomendations: function(){
			this.taulaProperties.clothes = [];
			this.taulaProperties.questionClothes = [];
			this.taulaProperties.answerClothes = [];
			this.taulaProperties.pickedClothes = [];
			this.taulaProperties.preferred = new Map();
			this.taulaProperties.punctuated = new Map();
			this.taulaProperties.recomendation = [];
		},
		getRecomendation: function(){
			if(!this.recomendation){
				this.punctuate();
				this.sortByPunctuation();
			}
			else{
				this.resetRecomendations();
				this.createClothes();
			}
			this.recomendation = !this.recomendation;
		}
  },
  created: function(){
	  this.createClothes();
  }
}
</script>

